import React, { useEffect, useState } from "react";
import useHttpClient from "../../../../hooks/useHttpClient";
import htmlParser from "react-html-parser";
import { NavLink } from "react-router-dom";
import "./posts.css";

const Posts = () => {
  const { sendRequest, isLoading } = useHttpClient();
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    const fetchMemories = async () => {
      try {
        const responseData = await sendRequest(
          `${process.env.REACT_APP_BACKEND_URL}/blog/first-three`
        );
        setPosts(responseData.posts);
      } catch (err) {}
    };
    fetchMemories();
  }, []);

  return (
    <div className="homepage-posts-wrapper">
      {!isLoading &&
        posts.length > 0 &&
        posts.map((post) => (
          <div key={post._id} className="homepage-post">
            <NavLink to={`/Blog/postId/${post._id}/Başlık/${post.title}`}>
              {post.title}
            </NavLink>
            <div className="homepage-post-content">
              {post.imageUrl ? (
                <div
                  style={{
                    backgroundImage: `url(${process.env.REACT_APP_ASSET_URL}/${post.imageUrl})`,
                  }}
                  className="homepage-post-image"
                />
              ) : (
              <div className="homepage-post-image">{post.title}</div>
              )}

              <div>
                <span>{post.date}</span> - <span>{post.username}</span>
              </div>
              <div className="homepage-post-description">
                {htmlParser(post.content.substring(0, 300) + "...")}
              </div>
            </div>
          </div>
        ))}
    </div>
  );
};

export default Posts;
